.contact{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: dark;
}

form{
    max-width: 600px;
    width: 100%;
    background-color: #fff;
    padding: 25px 25px 30px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    color: #333;
    margin: 25px;
} 

 form h2 {
    font-size: 30px;
    text-align: center;
    
}

form .input-box {
    margin-top: 20px;
}

.input-box .field{
    width: 100%;
    height: 50px;
    background: transparent;
    border: 2px solid #ddd;
    border-radius: 6px;
    padding: 15px;
    font-size: 16px;
    color: #333;
    margin-top: 8px;

}

.input-box .field.mess{
    height: 200px;
    
}

form button{
    width: 100%;
    height: 55px;
    background-color: #826afb;
    border: none;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(0, 0 ,0, .1);
    cursor: pointer;
    font-size: 16px;
    color: #fff;
    font-weight: 500;
    margin-top: 25px;
    transition: .5px;
}
form button:hover {
    background: #624ec6;
}